const idb = require("./idb.min.js");
import images from "../img/*.jpg";

/**
 * Common database helper functions.
 */
export default class DBHelper {
  /**
   * Database URL.
   * Change this to restaurants.json file location on your server.
   */
  static get DATABASE_URL() {
    const port = 1337; // Change this to your server port
    return `http://localhost:${port}/restaurants`;
  }

  /**
   * Fetch all restaurants.
   */
  static fetchRestaurants(callback) {
    if (navigator.onLine) {
      fetch(DBHelper.DATABASE_URL)
        .then(response => response.json())
        .then(restaurants => {
          const dbPromise = idb.open("restaurantsDB", 1, upgradeDB => {
            switch (upgradeDB.oldVersion) {
              case 0:
                upgradeDB.createObjectStore("restaurants", {
                  keyPath: "id"
                });
              case 1:
                const reviewsTable = upgradeDB.createObjectStore("reviews", {
                  keyPath: "id"
                });
                reviewsTable.createIndex("restaurant", "restaurant_id");
            }
          });

          dbPromise.then(db => {
            const tx = db.transaction("restaurants", "readwrite");
            let keyValStore = tx.objectStore("restaurants");
            restaurants.forEach(function(restaurant) {
              keyValStore.put(restaurant);
            });
            return keyValStore.getAll();
          });
          callback(null, restaurants);
        })
        .catch(function(error) {
          console.log("An error has occurred.", error);
          callback(error, null);
        });
    } else {
      const dbPromise = idb.open("restaurantsDB");

      dbPromise
        .then(function(db) {
          var tx = db.transaction("restaurants", "readonly");
          var store = tx.objectStore("restaurants");
          return store.getAll();
        })
        .then(function(response) {
          return response;
        })
        .then(function(restaurants) {
          callback(null, restaurants);
        })
        .catch(function(error) {
          console.log("An error has occurred.", error);
          callback(error, null);
        });
    }
  }

  /**
   * Fetch a restaurant by its ID.
   */
  static fetchRestaurantById(id, callback) {
    // fetch all restaurants with proper error handling.
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        const restaurant = restaurants.find(r => r.id == id);
        if (restaurant) {
          // Got the restaurant
          callback(null, restaurant);
        } else {
          // Restaurant does not exist in the database
          callback("Restaurant does not exist", null);
        }
      }
    });
  }

  /**
   * Fetch restaurants by a cuisine type with proper error handling.
   */
  static fetchRestaurantByCuisine(cuisine, callback) {
    // Fetch all restaurants  with proper error handling
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        // Filter restaurants to have only given cuisine type
        const results = restaurants.filter(r => r.cuisine_type == cuisine);
        callback(null, results);
      }
    });
  }

  /**
   * Fetch restaurants by a neighborhood with proper error handling.
   */
  static fetchRestaurantByNeighborhood(neighborhood, callback) {
    // Fetch all restaurants
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        // Filter restaurants to have only given neighborhood
        const results = restaurants.filter(r => r.neighborhood == neighborhood);
        callback(null, results);
      }
    });
  }

  /**
   * Fetch restaurants by a cuisine and a neighborhood with proper error handling.
   */
  static fetchRestaurantByCuisineAndNeighborhood(
    cuisine,
    neighborhood,
    callback
  ) {
    // Fetch all restaurants
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        let results = restaurants;
        if (cuisine != "all") {
          // filter by cuisine
          results = results.filter(r => r.cuisine_type == cuisine);
        }
        if (neighborhood != "all") {
          // filter by neighborhood
          results = results.filter(r => r.neighborhood == neighborhood);
        }
        callback(null, results);
      }
    });
  }

  /**
   * Fetch all neighborhoods with proper error handling.
   */
  static fetchNeighborhoods(callback) {
    // Fetch all restaurants
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        // Get all neighborhoods from all restaurants
        const neighborhoods = restaurants.map(
          (v, i) => restaurants[i].neighborhood
        );
        // Remove duplicates from neighborhoods
        const uniqueNeighborhoods = neighborhoods.filter(
          (v, i) => neighborhoods.indexOf(v) == i
        );
        callback(null, uniqueNeighborhoods);
      }
    });
  }

  /**
   * Fetch all cuisines with proper error handling.
   */
  static fetchCuisines(callback) {
    // Fetch all restaurants
    DBHelper.fetchRestaurants((error, restaurants) => {
      if (error) {
        callback(error, null);
      } else {
        // Get all cuisines from all restaurants
        const cuisines = restaurants.map((v, i) => restaurants[i].cuisine_type);
        // Remove duplicates from cuisines
        const uniqueCuisines = cuisines.filter(
          (v, i) => cuisines.indexOf(v) == i
        );
        callback(null, uniqueCuisines);
      }
    });
  }

  /**
   * Restaurant page URL.
   */
  static urlForRestaurant(restaurant) {
    return `./restaurant.html?id=${restaurant.id}`;
  }

  /**
   * Restaurant image URL.
   */
  static imageUrlForRestaurant(restaurant) {
    // return "images[restaurant.photograph]";
    return images[restaurant.photograph];
  }

  /**
   * Map marker for a restaurant.
   */
  static mapMarkerForRestaurant(restaurant, map) {
    // https://leafletjs.com/reference-1.3.0.html#marker
    const marker = new L.marker(
      [restaurant.latlng.lat, restaurant.latlng.lng],
      {
        title: restaurant.name,
        alt: restaurant.name,
        url: DBHelper.urlForRestaurant(restaurant)
      }
    );
    marker.addTo(map);
    return marker;
  }
  /* static mapMarkerForRestaurant(restaurant, map) {
    const marker = new google.maps.Marker({
      position: restaurant.latlng,
      title: restaurant.name,
      url: DBHelper.urlForRestaurant(restaurant),
      map: map,
      animation: google.maps.Animation.DROP}
    );
    return marker;
  } */

  /**
   * Store review
   * @param review
   */
  static postReview(review) {
    let offlineObj = {
      name: "postReview",
      data: review,
      object_type: "review"
    };

    if (!navigator.onLine && offlineObj.name === "postReview") {
      DBHelper.sendDataOnline(offlineObj);
      return;
    }

    let reviewObject = {
      name: review.name,
      rating: review.rating,
      comments: review.comments,
      restaurant_id: review.restaurant_id
    };
    const options = {
      method: "POST",
      body: JSON.stringify(reviewObject),
      headers: new Headers({
        "Content-Type": "application/json"
      })
    };

    fetch(`http://localhost:1337/reviews`, options)
      .then(response => {
        const contentType = response.headers.get("content-type");
        if (contentType && contentType.indexOf("application/json") !== -1) {
          return response.json();
        } else {
          return "udpated";
        }
      })
      .then(data => {
        const errorField = document.getElementById("review-form-error");
        errorField.remove();
      })
      .catch(error => console.warn(error));
  }

  static sendDataOnline(obj) {
    localStorage.setItem("ObjectToSend", JSON.stringify(obj.data));
    window.addEventListener("online", event => {
      let data = JSON.parse(localStorage.getItem("ObjectToSend"));
      if (data !== null) {
        if (obj.name === "postReview") {
          DBHelper.postReview(obj.data);
        }
        localStorage.removeItem("ObjectToSend");
      }
    });
  }

  /**
   * get Reviews by Id
   * @param id
   * @returns {Promise<Response>}
   */
  static getReviews(id) {
    return fetch(`http://localhost:1337/reviews?restaurant_id=${id}`)
      .then(function(response) {
        return response.json();
      })
      .then(reviews => {
        const dbPromise = idb.open("restaurantsDB");
        dbPromise.then(function(db) {
          if (!db) return;

          let tx = db.transaction("reviews", "readwrite");
          const store = tx.objectStore("reviews");
          if (Array.isArray(reviews)) {
            reviews.forEach(function(review) {
              store.put(review);
            });
          } else {
            store.put(reviews);
          }
        });
        return Promise.resolve(reviews);
      })
      .catch(error => {
        console.warn("no connection");
        return DBHelper.getObject("reviews", "restaurant", id).then(function(
          sReviews
        ) {
          console.log("offline");
          return Promise.resolve(sReviews);
        });
      });
  }

  /**
   * Update is_favorite on the restaurant
   * @param restaurantId
   * @param isFavorite
   */
  static updateFavorite(restaurantId, isFavorite) {
    const favorite = () => {
      if (isFavorite) {
        return false;
      } else {
        return true;
      }
    };

    let restaurantObject = {
      is_favorite: favorite()
    };
    const options = {
      method: "PUT",
      body: JSON.stringify(restaurantObject),
      headers: new Headers({
        "Content-Type": "application/json"
      })
    };

    fetch(`${this.DATABASE_URL}/${restaurantId}`, options).then(() => {
      const dbPromise = idb.open("restaurantsDB");
      dbPromise.then(db => {
        const tx = db.transaction("restaurants", "readwrite");
        const store = tx.objectStore("restaurants");
        store.get(restaurantId).then(restaurant => {
          restaurant.is_favorite = isFavorite;
          store.put(restaurant);
        });
      });
    });
  }
}
